<template>
  <div :class="`d-inline-block text-white line-hieght-1-2 ${app_name == 'pbxplus' ? 'position-relative ml-6' : 'position-absolute top-68px ml-6'}  mt-n4 opacity-0-8`">
    <div v-if="connection && app_name == 'pbxplus'" class="font-weight-normal font-size-14px my-1">{{ phone_number }}</div>
    <p v-if="connection" class="mb-0 font-size-12px ">{{ this.duration }}</p>
  </div>
</template>

<script>
  export default {
    props: {
      connection: Object,
      phone_number: String,
      app_name: String,
    },
    data() {
      return {
        duration: "",
        timeInterval: "",
      };
    },
    mounted: function() {
      //   incrementTimer() {
      var input = {
        day: 0,
        hours: 0,
        minutes: 0,
        seconds: 0,
      };
      var timestamp = new Date(input.day, input.hours, input.minutes, input.seconds);
      var interval = 1;
      this.timeInterval = setInterval(() => {
        timestamp = new Date(timestamp.getTime() + interval * 1000);
        this.duration = `${timestamp.getHours() < 10 ? "0" + timestamp.getHours() : timestamp.getHours()} :
                          ${timestamp.getMinutes() < 10 ? "0" + timestamp.getMinutes() : timestamp.getMinutes()} :
                          ${timestamp.getSeconds() < 10 ? "0" + timestamp.getSeconds() : timestamp.getSeconds()}`;
      }, Math.abs(interval) * 1000);
      //   },
    },
  };
</script>

